.fav_techs-item {
  position: relative;
  display: flex;
  width: 450px;
  background: repeating-linear-gradient(
    90deg, #0002 0%, #34347a55 50%, #0002 100%
  );
  background-size: 300% auto;
  margin: 10px;
  padding: 10px;

  --main_logo-size: 70px;
}
  .fav_techs-logos {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
  }
    .fav_techs-logo {
      background-color: white;
      border-radius: 50%;
      padding: 5px;
    }
    .fav_techs-tech {
      display: block;
      width: var( --main_logo-size );
      height: var( --main_logo-size );
      margin: 0 auto;
    }
    .fav_techs-sub_techs {
      position: absolute;
      bottom: 20px;
      left: var( --main_logo-size );
      display: flex;
      flex-direction: row-reverse;
      padding: 0;
      list-style: none;
      z-index: -1;
    }
      .fav_techs-sub_tech {
        margin: -10px;
        width: 40px;
        height: 40px;
      }
  .fav_techs-tech_name {
    position: absolute;
    left: calc( var( --main_logo-size ) + 20px );
  }
  .fav_techs-description {
    margin-top: 30px;
  }
.links {
  display: flex;
}
.links-item {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
  .links-item:last-child {
    margin-right: 0;
  }
  .links-logo {
    min-width: 50px;
    min-height: 50px;
    max-width: 50px;
    max-height: 50px;
    margin:5px;
  }
  .links-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: -webkit-max-content;
    min-width: -moz-max-content;
    min-width: max-content;
  }
    .links-mail-button {
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content;
    }
.homepage {
  display: grid;
  grid-template:
    ". ." 50px
    ". favTechs"
    ". links" 200px
    / 250px 1fr
}

.evolveye_avatar {
  position: absolute;
  left: 50px;
  bottom: 50px;
  width: 150px;
  height: 150px;
}

.fav_techs-wrapper {
  padding: 20px;
  grid-area: favTechs;
}
  .fav_techs-addition {
    display: block;
    margin: 20px 0 0 70px;
    color: #55a5;
    width: 450px;
    text-align: center;
  }

.links {
  grid-area: links;
  padding: 20px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: 150px;
  background-color: #eee;
  border-radius: 20px;
  box-shadow: 0 0 10px #0006 inset;
  color: black;
}
  .links-link {
    border-bottom: 1px dashed transparent;
    margin: 2px 0;
  }
  .links-link:hover {
    border-bottom-color: #4169e1;
    color: #4169e1;
  }

@media only screen and (max-width: 1400px) {
  .homepage {
    grid-template-columns: 150px 1fr;
  }

  .evolveye_avatar {
    left: 0;
    width: 150px;
    height: 150px;
  }

  .links {
    padding: 20px 10px;
    min-width: unset;
  }
}

@media only screen and (max-width: 1220px) {
  .homepage {
    grid-template:
      ". ." 50px
      "favTechs favTechs"
      ". links"
      / 150px 1fr
  }

  .evolveye_avatar {
    bottom: 10px;
  }

  .links {
    flex-direction: column;
    height: unset;
    width: unset;
    padding: 20px 10px;
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 1000px) {
  .homepage {
    grid-template:
      "favTechs"
      "links"
  }
  .fav_techs-wrapper {
    margin-bottom: 100px;
  }

  .fav_techs-wrapper {
    padding: 0;
  }
  .fav_techs-title {
    text-align: center;
  }
  .fav_techs-addition {
    margin: 0;
    width: unset;
  }

  .evolveye_avatar {
    top: 25px;
    left: 40px;
    margin-bottom: 0;
  }

  .fav_techs-wrapper {
    margin-left: auto;
    margin-right: auto;
  }
  .fav_techs-item {
    width: unset;
  }

  .links {
    margin: 0;
    border-radius: 0;
    padding: 20px;
  }
}
.loading_box {
  background: repeating-linear-gradient(
    90deg, #0002 0%, rgba(52, 52, 122, 0.333) 50%, #0002 100%
  );
  width: 100%;
  background-size: 300% auto;
  background-position: 0 100%;
  -webkit-animation: gradient 2s forwards linear infinite;
          animation: gradient 2s forwards linear infinite;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 0%
  }
  100% {
    background-position: -300% 0%
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 0%
  }
  100% {
    background-position: -300% 0%
  }
}

.github_repos > * {
  display: block;
  margin: 20px auto;
}
  .github_repos-title {
    text-align: center;
  }
  .github_repos-section {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
  }
    .github_repos-section-title {
      margin: 30px 0 20px -20px;
    }
    .github_repos-item {
      display: block;
      padding: 20px;
      background: repeating-linear-gradient(
        90deg, #0002 0%, rgba(52, 52, 122, 0.333) 50%, #0002 100%
      );
      width: 100%;
      background-size: 300% auto;
      background-position: 0 100%;
      transition: background-position .25s;
      cursor: pointer;
    }
    .github_repos-item:hover {
      background-position: -100% 0%;
    }
      .github_repos-tech_name {
        margin: 0 20px;
        font-size: 17px;
      }

      .github_repos-item-description {
        font-size: 14px;
      }
.project_page {
  display: grid;
  grid-template:
    "back  desc"
    "ctrls ctrls"
    "proj  proj"
    / -webkit-max-content 1fr;
  grid-template:
    "back  desc"
    "ctrls ctrls"
    "proj  proj"
    / max-content 1fr;
}
.project_page-homepage_link {
  grid-area: back;
  display: block;
  padding: 10px 20px;
  margin: 10px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: 40px;
  background: repeating-linear-gradient(
    90deg, #2f72cabb 0%, #3358b1bb 50%, #2f72cabb 100%
  );
  background-size: 300% auto;
  background-position: 0 100%;
  border-radius: 10px;
  box-shadow: 0 2px 5px 2px #0003;
  font-family: consolas;
  font-weight: bold;
  transition: background-position .25s;
}

.project_page-homepage_link:hover {
  background-position: -100% 0%;
}

.project_page-homepage_link:active {
  box-shadow: 0 2px 5px 2px #0003 inset;
}

.project_page-description {
  grid-area: desc;
}
.project_page-controls {
  grid-area: ctrls;
}
#project_wrapper {
  grid-area: proj;
}
* {
  box-sizing: border-box;
}

ul, ol,
div, p,
h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}
body {
  background-color: #112;
  background: repeating-linear-gradient(
    -125deg, #112 0%, rgb(28, 28, 59) 100%
  );
  margin: 0;
  color: #ddd5c8;
  font-family: sans-serif;
  overflow-x: hidden
}

#content {
  position: relative;
  min-height: 100vh;
  display: grid;
  grid-template:
    "page page github"
    / 250px 1fr 1fr
}

#page {
  grid-area: page
}

.github_repos {
  grid-area: github
}
  .github_repos-item,
  .loading_box {
    height: 100px;
    width: 325px;
    border-top: 2px solid #4169e1;
    opacity: 0.8;
  }
  .github_repos-item:hover {
    opacity: 1;
  }

@media only screen and (max-width: 1400px) {
  #content {
    grid-template:
      "page github"
      / 1fr 450px
  }
}

@media only screen and (max-width: 1220px) {
  #content {
    min-height: 100vh;
    display: grid;
    grid-template:
      "page github"
      / 1fr 1fr;
  }
}

@media only screen and (max-width: 1000px) {
  * {
    font-size: 20px !important;
  }

  #content {
    grid-template:
      "github"
      "page";
    padding-top: 200px;
  }

  .github_repos {
    margin-bottom: 100px;
  }

  .github_repos-section {
    width: unset;
  }

  .github_repos-item {
    width: unset;
    margin: 20px;
  }

  .github_repos-section-title {
    margin: 0;
    text-align: center;
  }
}
